.form-page {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-rows: 1fr 3fr;
  gap: 5px;
  background-color: #fff;
}

.images-page{
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
}

.loading-page{
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
}

.loading-page img{
  width: 120px;
}

.image-container{
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.image-container img{
  border: 1px solid #bbb;
  max-width: 400px;
}

.logo-container {
  display: grid;
  grid-template-columns: 1fr 1.15fr;
  height: 100px;
  padding: 0;
  margin-top: auto;
  margin-bottom: 20px;
}

.logo-container img{
  width: 100px;
  margin-left: auto;
  margin-right: 20px;
}

.logo-container h1{
  padding-top: 10px;
  color: #bbb;
}

.form-container {
  display: grid;
  place-items: center;
  margin-top: 20px;
  margin-bottom: auto;
  background-color: #fff;
}

.form-container form {
  top: 10px;
  width: 60%;
  max-width: 600px;
  display: grid;
  gap: 10px;
  border: 1px solid #bbb;
  border-radius: 5px;
  padding: 10px 30px 10px 30px;
}

.form.label {
  width: 100%;
  text-align: center;
  font-size: 20px;
  margin: 10px 0 10px 0;
}

.form-input {
  font-size: 16px;
  margin: 5px 0 20px 0;
  border: 1px solid #bbb;
  border-radius: 5px;
  padding: 3px 10px 3px 10px;
}

.checkbox-label {
  margin-bottom: 10px;
  font-size: 16px;
}

.checkbox-label input[type="checkbox"] {
  margin-right: 10px;
}

.submit-button-wrapper {
  display: grid;
  place-items: center;
  margin: 10px 0 10px 0;
}

.submit-button {
  height: 40px;
  width: 200px;
  background-color: #5bc1f6;
  color: white;
  border: 3px solid #5bc1f6;
  border-radius: 30px;
  font-weight: bold;
}

.submit-button:hover {
  background-color: white;
  color: black;
}
